import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/loginVC.vue'
    },
    {
      path:'/loginVC.vue',
      component:resolve => require(['../components/login/vc/loginVC.vue'],resolve)

    },
    {
      path:'/HomePage',
      component:resolve => require(['../components/home/vc/HomePage.vue'],resolve)

    }
  ]
})



